import React from "react";
import LoggedinHeader from "./LoggedinHeader";
import Sidebar from "../components/hotlineMessages/Sidebar";
import OrgSidebar from "../containers/Sidebar";
import AccSidebar from "../components/manageOrg/Sidebar";
import { Row, Layout } from "antd";
// import Sidebar from  "../containers/Sidebar";

const { Content } = Layout;
const PageNotFoundHeader = () => {
  const isAuthenticated = localStorage.getItem("user");
  const commonForAllUsers = JSON.parse(localStorage.getItem("user"));
  const isamcc = commonForAllUsers.user_data.is_amcc_user;
  const isorgcc = commonForAllUsers.user_data.is_org_cc_user;
  const isamweb = commonForAllUsers.user_data.is_am_web_user;

  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader />
      </Row>
      <Row className="content-center">
        <Layout>
          <Layout>
            {isamweb === true ? (
              <Sidebar />
            ) : isorgcc === true ? (
              <OrgSidebar />
            ) : isamcc === true ? (
              <AccSidebar />
            ): null}

            <Content className="">
              <Row className="py-3" style={{ justifyContent: "center" }}>
                <h1 className="mb-0">You don't have access rights! Please contact your administrator.</h1>
              </Row>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};

export default PageNotFoundHeader;