import React from "react";
import Logo from "../../src/assets/images/anonymoose-logo.svg";
import { Row, Layout } from "antd";

const { Content } = Layout;
const PageNotFound = () => {
  return (
    <>
      <Row className="px-4 py-3">
        {" "}
        <img src={Logo} alt="AnonyMoose" className="pr-4" />
      </Row>
      <Row className="content-center">
        <Layout>
          <Layout>
            <Row className="py-3" style={{ justifyContent: "center" }}>
              <h1 className="mb-0">page not found</h1>
            </Row>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};

export default PageNotFound;